<template>
<div id="contract-crate">
    <div class="col-12">
        <div class="card border-0">
            <div class="card-body">
                <h1 class="fw-bold">Lập hợp đồng</h1>
                <div class="row">
                    <div class="col-6 col-md-6 col-sm-12 col-xs12">
                        <div class="form-group">
                            <label for="yourInput">Danh sách công ty</label>
                            <div class="input-group">
                                <input disabled type="text" class="form-control" placeholder="Danh sách công ty" v-model.lazy="contractState.Contract.company_name" />

                                <button class="btn btn-outline-secondary" type="button" id="button-addon3" @click="openModalCompany">
                                    <i class="fa-thin fa-magnifying-glass"></i>
                                </button>
                            </div>
                        </div>
                    </div>

                    <div class="col-6 col-md-6 col-sm-12 col-xs12">
                        <div class="form-group">
                            <label for="yourInput">Đầu mối liên hệ</label>
                            <div class="input-group">
                                <input disabled type="text" class="form-control" placeholder="Đầu mối liên hệ" v-model.lazy="contractState.Contract.customer_name" />
                                <button class="input-group-text" @click="addCustomer" title="Thêm người liên hệ">
                                    <i class="fa-solid fa-plus"></i>
                                </button>
                                <button class="btn btn-outline-secondary" type="button" id="button-addon2" @click="openModalCustomer">
                                    <i class="fa-thin fa-magnifying-glass"></i>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-6">
                        <div class="form-group">
                            <label for="">Số hợp đồng</label>
                            <input placeholder="Nhập số hợp đồng" type="text" v-model="contractState.Contract.contract_number" class="form-control" />
                        </div>
                    </div>
                    <div class="col-6">
                        <div class="form-group">
                            <label for="">Ngày hợp đồng</label>
                            <date-picker v-model="contractState.Contract.contract_date" :config="{
                    format: 'DD/MM/YYYY',
                    useCurrent: false,
                  }"></date-picker>
                        </div>
                    </div>
                </div>

                <div class="row">
                    <div class="col-12">
                        <div class="form-group">
                            <label for="">Ghi chú hợp đồng</label>
                            <textarea class="form-control" rows="1" v-model.lazy="contractState.Contract.contract_note" placeholder="Nhập ghi chú hợp đồng" />
                            </div>
            </div>
          </div>
          <div class="row">
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Loại hợp đồng</label>
                <select
                  class="form-select"
                  v-model.lazy="contractState.Contract.contract_type"
                >
                  <option value="0">Đăng ký mới</option>
                  <option value="1">Gia hạn</option>
                </select>
              </div>
            </div>
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Tháng đăng ký hợp đồng</label>
                <select
                  class="form-select"
                  v-model.lazy="contractState.Contract.contract_expired_month"
                >
                  <option
                    :value="item.id"
                    v-for="item in contractState.dateChooses"
                    :key="item.id"
                  >
                    {{ item.text }}
                  </option>
                </select>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Ngày bắt đầu</label>
                <date-picker
                  v-model="contractState.Contract.contract_date_start"
                  :config="{
                    format: 'DD/MM/YYYY',
                    useCurrent: false,
                  }"
                ></date-picker>
              </div>
            </div>
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Ngày hết hạn</label>
                <date-picker
                  v-model="contractState.Contract.contract_date_end"
                  :config="{
                    format: 'DD/MM/YYYY',
                    useCurrent: false,
                  }"
                ></date-picker>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Trạng thái hợp đồng</label>
                <select
                  class="form-select"
                  v-model="contractState.Contract.contract_status"
                >
                  <option value="0">Mới tạo lập</option>
                  <option value="1">Đã trình ký</option>
                  <option value="2">Đã gửi khách hàng</option>
                  <option value="3">Hoàn thiện</option>
                  <option value="4">Đã làm thanh toán</option>
                </select>
              </div>
            </div>
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Trạng thái thanh toán</label>
                <select
                  class="form-select"
                  v-model="contractState.Contract.contract_payment"
                >
                  <option value="0">Chưa thanh toán</option>
                  <option value="1">Đã nhận tiền nhưng chưa nộp tiền</option>
                  <option value="2">
                    Đã xuất hóa đơn nhưng chưa thu tiền khách hàng
                  </option>
                  <option value="3">Đã nộp tiền</option>
                  <option value="4">Đã nộp tiền và làm thanh toán</option>
                </select>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Khu vực tính doanh thu</label>
                <select
                  class="form-select"
                  v-model="contractState.Contract.contract_area"
                >
                  <option value="0">Thành phố</option>
                  <option value="1">Huyện (Khác)</option>
                </select>
              </div>
            </div>
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Mã hợp đồng/Thanh toán</label>
                <input
                  v-model="contractState.Contract.contract_code"
                  type="text"
                  placeholder="Mã hợp đồng/Mã thanh toán"
                  class="form-control"
                />
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Mã BHXH/Số lượng hóa đơn</label>
                <input
                  v-model="contractState.Contract.extra1"
                  type="text"
                  placeholder="Mã BHXH/Số lượng hóa đơn"
                  class="form-control"
                />
              </div>
            </div>
            <div class="col-6 col-md-6 col-sm-12 col-xs12">
              <div class="form-group">
                <label for="">Ký hiệu/Mẫu số</label>
                <input
                  v-model="contractState.Contract.extra2"
                  type="text"
                  placeholder="Ký hiệu/Mẫu số"
                  class="form-control"
                />
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-6">
              <div class="form-group">
                <label for="">Tỷ lệ hợp đồng</label>
                <select v-model="contractState.Contract.ratio_id" class="form-select">
                  <option value="0">--Hãy chọn dữ liệu---</option>
                  <option
                    :value="item.ratio_id"
                    v-for="item in contractState.ratios"
                    :key="item.ratio_id"
                  >
                    {{ item.ratio_name }}
                  </option>
                </select>
              </div>
            </div>
          </div>

          <div class="row mt-2">
            <div class="col-12">
              <div class="btn-group mb-2 mt-1 d-inline">
                <button
                  type="button"
                  class="btn btn-danger dropdown-toggle"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  <i class="fa-duotone fa-circle-plus"></i> Thêm
                </button>
                <ul class="dropdown-menu">
                  <li>
                    <a
                      class="dropdown-item hand"
                      @click="showModalDetail(false)"
                      title="Thêm dịch vụ"
                      ><i class="fa-thin fa-grid-2-plus"></i> Thêm dịch vụ</a
                    >
                  </li>
                  <li>
                    <a class="dropdown-item hand" title="Chọn mẫu hợp đồng"
                      ><i class="fa-thin fa-layer-plus"></i> Chọn mẫu hợp đồng</a
                    >
                  </li>
                  <li>
                    <a class="dropdown-item hand" title="Thêm phiếu đăng ký"
                      ><i class="fa-thin fa-user-plus"></i> Thêm phiếu đăng ký</a
                    >
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-12">
              <div class="table-responsive table-scroll">
                <table
                  class="table table-dark-gray align-middle p-4 mb-0 table-hover mt-2"
                >
                  <thead class="table-dark">
                    <tr>
                      <th class="rounded-start">#</th>
                      <th width="40%">Tên dịch vụ</th>
                      <th width="10%">Đơn vị tính</th>
                      <th width="7%">Số lượng</th>
                      <th>Tiền trước thuế</th>
                      <th>Tiền sau thuế</th>
                      <th class="rounded-end" width="10%"></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="(detail, index) in contractState.Contract.details"
                      :key="detail.detail_id + index"
                    >
                      <td>{{ index + 1 }}</td>
                      <td>{{ detail.detail_content }}</td>
                      <td>{{ detail.detail_unit }}</td>
                      <td>{{ detail.detail_quantity }}</td>
                      <td>{{ detail.detail_product_price }}</td>
                      <td></td>
                      <td>
                        <button
                          class="btn btn-primary-soft btn-sm"
                          @click="showModalDetail(true, index)"
                        >
                          Chọn
                        </button>
                      </td>
                    </tr>

                    <!-- <contractDetail
                            v-for="(detail, index) in contractState.Contract.details"
                            :key="'rows_' + index + '_' + detail.contract_id"
                            v-bind:detail="detail"
                            v-bind:index="index"
                          /> -->
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <modalAddDetail ref="modalDetail"></modalAddDetail>
    <modal-companies
      ref="modalCompanies"
      @chooseCompany="chooseCompany"
    ></modal-companies>
    <modal-customers ref="modalcustomers" @chooseCustomer="chooseCustomer">
    </modal-customers>
  </div>
</template>

<script>
import {
    mapGetters
} from "vuex";
import ModalCompanies from "@/extends/ModalCompanies.vue";
import ModalCustomers from "@/extends/ModalCustomers.vue";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import contractDetail from "../../components/contracts/Details.vue";
import modalAddDetail from "@/extends/ModalAddDetail2.vue";
import moment from "moment";

export default {
    components: {
        ModalCompanies,
        ModalCustomers,
        datePicker,
        contractDetail,
        modalAddDetail,
    },
    computed: {
        ...mapGetters("contracts", ["contractState"]),
    },
    created() {
        if (this.$route.params.id) {
            this.contractState.contract_id = this.$route.params.id;
            this.$store.dispatch("contracts/getContract", this.contractState.contract_id);
        }
        this.$store.dispatch("contracts/getRatio");
    },
    watch: {
        "contractState.Contract.contract_date": {
            handler(newValue) {
                this.contractState.Contract.contract_date_start = newValue;
                this.changeDate();
            },
            immediate: false,
            deep: true,
        },
        "contractState.Contract.contract_expired_month": {
            handler() {
                this.changeDate();
            },
            immediate: false,
            deep: true,
        },
    },
    methods: {
        showModalDetail(flagUpdate, index) {
            this.$refs.modalDetail.show(flagUpdate, index);
        },
        addCustomer() {},
        openModalCompany() {
            this.$refs.modalCompanies.show();
        },
        chooseCompany(item) {
            this.contractState.Contract.company_name = item.company_name;
            this.contractState.Contract.company_id = item.company_id;
        },
        chooseCustomer(item) {
            this.contractState.Contract.customer_name = item.customer_name;
            this.contractState.Contract.customer_id = item.customer_id;
        },
        openModalCustomer() {
            this.$refs.modalcustomers.show();
        },

        changeDate() {
            let dateStart = moment(
                this.contractState.Contract.contract_date_start,
                "DD/MM/YYYY"
            );
            let newDate = dateStart
                .clone()
                .add(this.contractState.Contract.contract_expired_month, "months");
            this.contractState.Contract.contract_date_end = newDate.format("DD/MM/YYYY");
        },
    },
};
</script>
