<template>
  <b-modal
    id="modal-detail"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="lg"
    ref="modalDetail"
    ignore-enforce-focus-selector="span.select2-container"
  >
    <b-row>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Nhóm dịch vụ</label>
          <select2
            v-model="Detail.cate_id"
            :options="select.categories"
            :settings="settings"
          />
        </div>
      </b-col>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Dịch vụ</label>
          <select2
            v-model="Detail.product_id"
            :options="product"
            :settings="settings"
            @change="getProduct()"
          />
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Tiền trước thuế</label>
          <money
            class="form-control"
            v-bind="money"
            v-model="Detail.detail_product_price"
          />
        </div>
      </b-col>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Tiền thành phần</label>
          <!-- <money
            class="form-control"
            v-bind="money"
            v-model="Detail.detail_product_component"
          /> -->
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Mã BHXH/Số lượng hóa đơn</label>
          <input
            placeholder="Mã BHXH/Số lượng hóa đơn"
            type="text"
            class="form-control"
            v-model="Detail.extra1"
          />
        </div>
      </b-col>
      <b-col cols="6">
        <div class="form-group">
          <label for="">Ký hiệu/Mẫu số</label>
          <input
            placeholder="Ký hiệu/Mẫu số"
            type="text"
            class="form-control"
            v-model="Detail.extra2"
          />
        </div>
      </b-col>
    </b-row>
    <b-row class="mt-1">
      <b-col cols="12">
        <div class="form-check form-switch">
          <input
            class="form-check-input"
            type="checkbox"
            role="switch"
            v-model="Detail.is_device"
          />
          <label class="form-check-label" for="flexSwitchCheckChecked">Thiết bị</label>
        </div>
      </b-col>
      <b-col cols="12" v-show="Detail.is_device"> </b-col>
    </b-row>

    <b-row>
      <b-col class="12">
        <div class="form-group">
          <label for="">Ghi chú</label>
          <textarea
            type="text"
            placeholder="Ghi chú"
            class="form-control"
            v-model="Detail.detail_note"
            rows="3"
          />
        </div>
      </b-col>
    </b-row>
    <template #modal-title>Thêm dịch vụ </template>
    <template #modal-footer>
      <button @click="saveDetail" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('modal-detail')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import { mapGetters } from "vuex";
import Select2 from "v-select2-component";
import { Money } from "v-money";
export default {
  components: {
    Select2,
    Money,
  },
  data() {
    return {
      settings: {
        placeholder: "--Hãy chọn dữ liệu--",
        theme: "bootstrap-5",
        width: "100%",
      },
      select: {
        categories: [],
        products: [],
      },
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " ",
        precision: 4,
        masked: false,
      },  
      isUpdate: false,
      index: 0,
      isSaveData: false,
    };
  },

  watch: {
    "Detail.product_id": {
      handler: function (newValue) {
        let product = this.products.filter((x) => {
          return x.product_id == newValue;
        })[0];
        if (product) {
          let dataDetail = {
            detail_product_price: product.product_price,
            product_id: product.product_id,
          };
          this.$store.dispatch("detail/updateData", dataDetail);
        }
      },
      deep: true,
      immediate: false,
    },
  },
  computed: {
    product() {
      return this.select.products.filter((product) => {
        return product.cate_id == this.Detail.cate_id;
      });
    },
    ...mapGetters("detail", ["Detail"]),
    ...mapGetters("contracts", ["contractState"]),
    ...mapGetters("products", ["products"]),
    ...mapGetters("categories", ["categories"]),
  },
  created() {},
  methods: {
    show(flagUpdate = false, index) {
      if (!flagUpdate) this.$store.dispatch("detail/setDetailNull");
      else {
        this.isUpdate = true;
        this.index = index;
        let data = this.contractState.Contract.details[index];
        this.$store.dispatch("detail/getDetail", data);
      }
      this.getCategories();
      this.getProducts();
      this.$bvModal.show("modal-detail");
    },
    saveDetail() {
      this.saveDetail = true;
      if (!this.isUpdate) this.$store.dispatch("contracts/addDetail", this.Detail);
      else {
        this.$store.dispatch("contracts/updateDetail", {
          detail: this.Detail,
          index: this.index,
        });
      }
      this.$bvModal.hide("modal-detail");
    },

    async getCategories() {
      await this.$store.dispatch("categories/getCategories");
      this.select.categories = this.categories.map((c) => {
        return { id: c.cate_id, text: c.cate_name };
      });
    },

    async getProducts() {
      await this.$store.dispatch("products/getProducts");
      this.select.products = this.products.map((c) => {
        return {
          id: c.product_id,
          text: c.product_name,
          cate_id: c.cate_id,
          product_price: c.product_price,
          product_component_price: c.product_component_price,
        };
      });
    },
  },
};
</script>

<style scoped>
.select2 {
  width: 100% !important;
}
</style>
